.resume {
    padding-top: 120px;
    &__title {
        position: relative;
        text-align: center;
    }
    &__wrapper {
        padding: 60px 65px 0 65px;
        display: grid;
        grid-template-columns: repeat(2, 425px);
        column-gap: 135px;
    }
    &__column {
        &-title {
            position: relative;
            padding-left: 35px;
            &::before {
                content: "";
                position: absolute;
                width: 15px;
                height: 15px;
                background-color: $main-color;
                border-radius: 100%;
                left: -7px;
                top: 50%;
                transform: translateY(-50%);
            }
        }
        ul {
            list-style-type: none;
            li {
                position: relative;
                &:after {
                    content: "";
                    position: absolute;
                    width: 1px;
                    height: calc(100% + 5px);
                    top: -5px;
                    left: 0;
                    background-color: black;
                }
            }
        }
    }
    &__item {
        position: relative;
        &:before {
            content: '';
            height: 1px;
            width: 45px;
            position: absolute;
            top: 50px;
            background-color: #000;

        }
        &-head {
            padding: 30px 0 0 100px;
            h4 {
                margin-bottom: 0;
            }
        }
        &-icon {
            position: absolute; 
            top: 33px;
            left: 45px;
            @include before_circle;    
            img {
                position: absolute;
                left: 17px;
                top: 13px;
            }      
        }
        &-location {
            font-size: 12px;
            line-height: 18px;
            margin-top: 5px;
        }
        &-body {
            padding: 12px 0 0 43px;
            font-size: 13px;
            line-height: 18px;
        }
    }
}